.body-login {
  background: url(../images/bg-login.jpg) no-repeat;
  background-size: cover;
  background-position: bottom;
  height: 100vh; }
  .body-login .container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 100%; }
    .body-login .container .logo {
      text-align: center; }
      .body-login .container .logo img {
        width: 85%; }
    .body-login .container .form-login {
      margin: auto;
      margin-bottom: 1rem; }
      .body-login .container .form-login .form-control {
        padding: 1rem .75rem;
        line-height: 1; }
      .body-login .container .form-login .username {
        background-image: url(../images/user.png);
        background-repeat: no-repeat;
        background-position: 10px;
        background-size: 8%;
        border-color: #FC5C3E;
        text-align: center;
        border-radius: 10px;
        color: #3e3f42; }
        .body-login .container .form-login .username:focus {
          background-color: #fff;
          border-color: transparent;
          box-shadow: 0 0 0 2px rgba(54, 69, 64, 0.3); }
      .body-login .container .form-login .input-pass {
        position: relative; }
        .body-login .container .form-login .input-pass .password {
          display: inline;
          background-image: url(../images/security.png);
          background-repeat: no-repeat;
          background-position: 12px;
          background-size: 7%;
          border-color: #FC5C3E;
          text-align: center;
          border-radius: 10px;
          color: #3e3f42; }
          .body-login .container .form-login .input-pass .password:focus {
            background-color: #fff;
            border-color: transparent;
            box-shadow: 0 0 0 2px rgba(54, 69, 64, 0.3); }
        .body-login .container .form-login .input-pass .eyes-hide,
        .body-login .container .form-login .input-pass .eyes-show {
          position: absolute;
          top: 0;
          right: 0;
          width: 35px;
          height: 100%;
          transition: all .3s;
          background: transparent;
          transform: translateX(-50%);
          border: none; }
        .body-login .container .form-login .input-pass .eyes-hide {
          display: none;
          transition: all .3s; }
        .body-login .container .form-login .input-pass .eyes-show {
          display: block;
          transition: all .3s; }
      .body-login .container .form-login .button {
        width: 100%;
        background: linear-gradient(to right, #e64528 0, #ce492b 0, #ef722c 100%);
        border-color: transparent;
        text-align: center;
        border-radius: 10px;
        padding: 1rem .75rem;
        border-color: #FC5C3E;
        color: rgba(255, 255, 255, 0.9);
        font-weight: 600;
        line-height: 1; }
        .body-login .container .form-login .button:active {
          background: #fd3916;
          border-color: rgba(255, 100, 100, 0.3); }
        .body-login .container .form-login .button:focus {
          background: #fd3916;
          border-color: rgba(255, 100, 100, 0.3);
          box-shadow: 0 0 0 0.1rem rgba(255, 100, 100, 0.3); }
        .body-login .container .form-login .button:hover {
          background: #fd3916;
          border-color: rgba(255, 100, 100, 0.3);
          box-shadow: 0 0 0 0.1rem rgba(255, 100, 100, 0.3); }
        .body-login .container .form-login .button img {
          width: 100%; }
    .body-login .container .sing-up {
      text-align: center;
      color: rgba(255, 255, 255, 0.9);
      font-size: 13px; }
      .body-login .container .sing-up span {
        font-family: "Montserrat"; }
      .body-login .container .sing-up a {
        color: #f99c4d;
        padding: 0 5px;
        text-decoration: none; }
    .body-login .container .social {
      display: flex;
      justify-content: space-evenly; }
