.body-login {
  background: url(../images/bg-login.jpg) no-repeat;
  background-size: cover;
  background-position: bottom;
  height: 100vh;
  .container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 100%;
    .logo {
      text-align: center;
      img {
        width: 85%;
      }
    }
    .form-login {
      margin: auto;
      margin-bottom: 1rem;
      .form-control {
        padding: 1rem .75rem;
        line-height: 1;
      }
      .username {
        background-image: url(../images/user.png);
        background-repeat: no-repeat;
        background-position: 10px;
        background-size: 8%;
        border-color: #FC5C3E;
        text-align: center;
        border-radius: 10px;
        color: #3e3f42;
        &:focus {
          background-color: #fff;
          border-color: transparent;
          box-shadow: 0 0 0 2px rgba(54, 69, 64, .3);
        }
      }
      .input-pass {
        position: relative;
        .password {
          display: inline;
          background-image: url(../images/security.png);
          background-repeat: no-repeat;
          background-position: 12px;
          background-size: 7%;
          border-color: #FC5C3E;
          text-align: center;
          border-radius: 10px;
          color: #3e3f42;
          &:focus {
            background-color: #fff;
            border-color: transparent;
            box-shadow: 0 0 0 2px rgba(54, 69, 64, .3);
          }
        }
        .eyes-hide,
        .eyes-show {
          position: absolute;
          top: 0;
          right: 0;
          width: 35px;
          height: 100%;
          transition: all .3s;
          background: transparent;
          transform: translateX(-50%);
          border: none;
        }
        .eyes-hide {
          display: none;
          transition: all .3s;
        }
        .eyes-show {
          display: block;
          transition: all .3s;
        }
      }
      .button {
        width: 100%;
        background: linear-gradient(to right,#e64528 0,#ce492b 0,#ef722c 100%);
        border-color: transparent;
        text-align: center;
        border-radius: 10px;
        padding: 1rem .75rem;
        border-color: #FC5C3E;
        color: rgba(255, 255, 255, .9);
        font-weight: 600;
        line-height: 1;
        &:active {
          background: #fd3916;
          border-color: rgba(255, 100, 100, .3);
        }
        &:focus {
          background: #fd3916;
          border-color: rgba(255, 100, 100, .3);
          box-shadow: 0 0 0 0.1rem rgba(255, 100, 100, .3);
        }
        &:hover {
          background: #fd3916;
          border-color: rgba(255, 100, 100, .3);
          box-shadow: 0 0 0 0.1rem rgba(255, 100, 100, .3);
        }
        img {
          width: 100%;
        }
      }
    }
    .sing-up {
      text-align: center;
      color: rgba(255, 255, 255, .9);
      font-size: 13px;
      span {
        font-family: "Montserrat";
      }
      a {
        color: #f99c4d;
        padding: 0 5px;
        text-decoration: none;
      }
    }
    .social {
      display: flex;
      justify-content: space-evenly;
    }
  }
}